import * as yup from 'yup';
import { MIN_PASSWORD_LENGTH } from 'constansts/form';
import { EOperation } from 'interfaces/operation';

const emailField = yup.string().email(' e-mail').required('  ')
const nameField = yup.string().required('  ')
const passwordField = yup
  .string()
  .required('  ')
  .min(MIN_PASSWORD_LENGTH, ` ${MIN_PASSWORD_LENGTH} `);
const passwordRepeatField = yup
  .string()
  .required('  ')
  .test((value, ctx) => {
    if (ctx.from && value !== ctx.from[0].value.password) {
      return ctx.createError({ message: '  ' });
    }
    return true;
  })

export const registrationSchema = yup.object({
  email: emailField,
  password: passwordField,
  passwordRepeat: passwordRepeatField,
});

export const loginSchema = yup.object({
  email: emailField,
  password: passwordField,
});

export const addOperationSchema = yup.object({
  amount: yup.number().positive('  > 0').required('  '),
  name: yup.string().required('  '),
  desc: yup.string(),
  categoryId: yup.string().required('  '),
  type: yup.mixed<EOperation>().oneOf(Object.values(EOperation)),
});

export const changePasswordSchema = yup.object({
  password: passwordField,
  newPassword: passwordField,
});

export const changeProfileDataSchema = yup.object({
  name: nameField
});

export const categorySchema = yup.object({
  name: nameField,
  photo: yup.string()
});